import { takeEvery, call, put, delay } from 'redux-saga/effects';
import { getPersonDetails } from './personDetails';
import { fetchPersonDetails, fetchPersonDetailsError, fetchPersonDetailsSuccess } from './moviesSlice';

function* fetchPersonDetailsHandler() {

  try {
    yield delay(300);
    const personDetails = yield call(getPersonDetails);
    yield put(fetchPersonDetailsSuccess(personDetails));
  } catch (error) {
    yield put(fetchPersonDetailsError(error));
  }
}

export function* personDetailsSaga() {
  yield takeEvery(fetchPersonDetails.type, fetchPersonDetailsHandler);
}