import { takeEvery, call, put, delay } from 'redux-saga/effects';
import { getMovieDetails } from './movieDetails';
import { fetchMovieDetails, fetchMovieDetailsError, fetchMovieDetailsSuccess } from './moviesSlice';

function* fetchMovieDetailsHandler() {

  try {
    yield delay(300);
    const movieDetails = yield call(getMovieDetails);
    yield put(fetchMovieDetailsSuccess(movieDetails));
  } catch (error) {
    yield put(fetchMovieDetailsError(error));
  }
}

export function* movieDetailsSaga() {
  yield takeEvery(fetchMovieDetails.type, fetchMovieDetailsHandler);
}