import { takeEvery, call, put, delay } from "redux-saga/effects";
import { getPopularMovies, getTotalPagesMovies } from "./movies";
import {
    fetchMovies,
    fetchMoviesSuccess,
    fetchMoviesError
} from "./moviesSlice";

function* fetchMoviesHandler({ payload: currentPage }) {
    try {
        yield delay(300);
        const movies = yield call(getPopularMovies, currentPage);
        const totalPagesMovies = yield call(getTotalPagesMovies);
        yield put(fetchMoviesSuccess({ totalPagesMovies, movies }));
    } catch (error) {
        yield put(fetchMoviesError(error));
    }
}

export function* moviesSaga() {
    yield takeEvery(fetchMovies.type, fetchMoviesHandler);
}