import { takeEvery, call, put, delay } from "redux-saga/effects";
import { getPopularPeople, getTotalPagesPeople } from "./people";
import {
    fetchPeople,
    fetchPeopleSuccess,
    fetchPeopleError
} from "./moviesSlice";

function* fetchPeopleHandler({ payload: currentPage }) {
    try {
        yield delay(300);
        const people = yield call(getPopularPeople, currentPage);
        const totalPagesPeople = yield call(getTotalPagesPeople);
        yield put(fetchPeopleSuccess({ totalPagesPeople, people }));
    } catch (error) {
        yield put(fetchPeopleError(error));
    }
}

export function* peopleSaga() {
    yield takeEvery(fetchPeople.type, fetchPeopleHandler);
}