import { takeEvery, call, put, delay } from 'redux-saga/effects';
import { getPersonCredits } from './personCredits';
import { fetchPersonCredits, fetchPersonCreditsError, fetchPersonCreditsSuccess } from './moviesSlice';

function* fetchPersonCreditsHandler() {

  try {
    yield delay(300);
    const personCredits = yield call(getPersonCredits);
    yield put(fetchPersonCreditsSuccess(personCredits));
  } catch (error) {
    yield put(fetchPersonCreditsError(error));
  }
}

export function* personCreditsSaga() {
  yield takeEvery(fetchPersonCredits.type, fetchPersonCreditsHandler);
}