import { takeEvery, call, put, delay } from 'redux-saga/effects';
import { getMovieCredits } from './movieCredits';
import { fetchMovieCredits, fetchMovieCreditsError, fetchMovieCreditsSuccess } from './moviesSlice';

function* fetchMovieCreditsHandler() {

  try {
    yield delay(300);
    const movieCredits = yield call(getMovieCredits);
    yield put(fetchMovieCreditsSuccess(movieCredits));
  } catch (error) {
    yield put(fetchMovieCreditsError(error));
  }
}

export function* movieCreditsSaga() {
  yield takeEvery(fetchMovieCredits.type, fetchMovieCreditsHandler);
}