import axios from "axios";
import { url_back, url_front_movie } from "./api";

export const getPopularMovies = async (currentPage) => {
    const url = `${url_front_movie}popular${url_back}&page=${currentPage}`;
    try {
        const response = await axios.get(url);
        return response.data;
    } catch (error) {
        throw error;
    }
};

export const getTotalPagesMovies = async () => {
    for (let i = 1; i <= 1000000; i++) {
        const response = await axios.get(`${url_front_movie}popular${url_back}&page=${i}`);
        if (response.data.results && response.data.results.length > 0) {
            return i;
        }
    }
    return 1;
};