import { all } from "redux-saga/effects";
import { moviesSaga } from "./moviesSaga";
import { genresSaga } from "./genresSaga";
import { peopleSaga } from "./peopleSaga";
import { movieCreditsSaga } from "./movieCreditSaga";
import { movieDetailsSaga } from "./movieDetailsSaga";
import { personCreditsSaga } from "./personCreditsSaga";
import { personDetailsSaga } from "./personDetailsSaga";

export default function* rootSaga() {
    yield all([
        moviesSaga(),
        peopleSaga(),
        genresSaga(),
        movieCreditsSaga(),
        movieDetailsSaga(),
        personCreditsSaga(),
        personDetailsSaga(),
    ]);
}