import { createSlice } from '@reduxjs/toolkit';

const moviesSlice = createSlice({
    name: 'movies',
    initialState: {
        movies: {},
    },
    reducers: {

        fetchMovies: (state, { payload: currentPage }) => {
            state.loading = true;
            state.currentPage = currentPage;
        },
        fetchMoviesSuccess: (state, { payload: { totalPagesMovies, data } }) => {
            state.loading = false;
            state.totalPagesMovies = totalPagesMovies;
            state.popularMovies = data;
        },
        fetchMoviesError: (state) => {
            state.loading = false;
        },
        fetchPeople: (state, { payload: currentPage }) => {
            state.loading = true;
            state.currentPage = currentPage;
        },
        fetchPeopleSuccess: (state, { payload: { totalPagesPeople, data } }) => {
            state.loading = false;
            state.totalPagesPeolpe = totalPagesPeople;
            state.popularPeople = data;
        },
        fetchPeopleError: (state) => {
            state.loading = false;
        },
        fetchGenres: (state) => {
            state.loading = true;
        },
        fetchGenresSuccess: (state, { payload: genres }) => {
            state.loading = false;
            state.genres = genres;
        },
        fetchGenresError: (state) => {
            state.loading = false;
        },
        fetchMovieCredits: (state) => {
            state.loading = true;
        },
        fetchMovieCreditsSuccess: (state, { payload: movieCredits }) => {
            state.loading = false;
            state.movieCredits = movieCredits;
        },
        fetchMovieCreditsError: (state) => {
            state.loading = false;
        },
        fetchMovieDetails: (state) => {
            state.loading = true;
        },
        fetchMovieDetailsSuccess: (state, { payload: movieDetails }) => {
            state.loading = false;
            state.movieDetails = movieDetails;
        },
        fetchMovieDetailsError: (state) => {
            state.loading = false;
        },
        fetchPersonCredits: (state) => {
            state.loading = true;
        },
        fetchPersonCreditsSuccess: (state, { payload: personCredits }) => {
            state.loading = false;
            state.personCredits = personCredits;
        },
        fetchPersonCreditsError: (state) => {
            state.loading = false;
        },
        fetchPersonDetails: (state) => {
            state.loading = true;
        },
        fetchPersonDetailsSuccess: (state, { payload: personDetails }) => {
            state.loading = false;
            state.personDetails = personDetails;
        },
        fetchPersonDetailsError: (state) => {
            state.loading = false;
        },
    },
});

export const {
    fetchMovies,
    fetchMoviesSuccess,
    fetchMoviesError,
    fetchPeople,
    fetchPeopleSuccess,
    fetchPeopleError,
    fetchGenres,
    fetchGenresSuccess,
    fetchGenresError,
    fetchMovieCredits,
    fetchMovieCreditsSuccess,
    fetchMovieCreditsError,
    fetchMovieDetails,
    fetchMovieDetailsSuccess,
    fetchMovieDetailsError,
    fetchPersonCredits,
    fetchPersonCreditsSuccess,
    fetchPersonCreditsError,
    fetchPersonDetails,
    fetchPersonDetailsSuccess,
    fetchPersonDetailsError
} = moviesSlice.actions;

export const selectMovies = state => state.movies.movies;
export const selectPeople = state => state.people.people;
export const selectGenres = state => state.movies.genres;

export const getMovieById = (state, movieId) => selectMovies(state).find(({ id }) => id === movieId);
export const getPersonById = (state, peopleId) => selectPeople(state).find(({ id }) => id === peopleId);

export const selectMoviesByQuery = (state, query) => {
    const movies = selectMovies(state);
    if (!query || query.trim() === "") {
        return movies;
    }

    return movies.filter(({ title }) =>
        title.toUpperCase().includes(query.trim().toUpperCase()));
}

export default moviesSlice.reducer;